.cb-home-never-lowball {
	display: flex;
	flex-direction: column;
	align-items: center;
padding: 0 15px;
}

@media (min-width: 1024px) {
	.cb-home-never-lowball {

		max-width: 1030px;
		margin: 0 auto;
	}
}

.cb-home-never-lowball__content {
	display: flex;
	flex-direction: column;
	/* gap: 2rem; */
	justify-content: space-between;
	align-items: center;
	/* padding: 3rem 1rem; */
	max-width: 100%;

	font-size: 24px;
	line-height: 1.5;
	font-weight: normal;
	font-family: "Poppins", sans-serif !important;
}
.cb-home-never-lowball__content--text{
	margin: 0;
}
@media (min-width: 1024px) {
	.cb-home-never-lowball__content {
		flex-direction: row;
		max-width: 100%;
		padding: 0;
	}
	.cb-home-never-lowball__content--img-block{
		flex: 0 0 40%;
		width: 60%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.cb-home-never-lowball__content--text{
		flex: 0 0 60%;
		width: 60%;
		margin: 0;
	}
}

.cb-home-never-lowball::before,
.cb-home-never-lowball::after {
	content: "";
	display: block;
	width: 100%;
	border: 1px solid #ff996b;
	background-color: #ff996b;
	margin-block: 30px;

	    font-family: "Poppins", sans-serif !important;
}
@media (min-width: 1024px) {
	.cb-home-never-lowball::before,
.cb-home-never-lowball::after {
	margin-block: 36px;
}
}